.portfolioPage {
  padding: 12px 12px 50px;
}
.currencySetting {
  margin-bottom: 12px;
  display: flex;
  gap: 5px;
  justify-content: flex-end;
  button {
    padding: 6px 12px;
    border-radius: 4px;
    border: none;
    background-color: #dadadb;
    color: white;
    cursor: pointer;
    font-size: 14px;
    &.active {
      background-color: #396aff;
    }
  }
}

.totalInvestInfo {
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.portfolioList {
  display: flex;
  flex-direction: column;
  gap: 12px;
  li {
    padding: 12px;
    display: flex;
    flex-direction: column;
    box-shadow: white 0px 7px 29px 0px;
    border-radius: 8px;
    h2 {
      margin-bottom: 12px;
    }
    span {
      margin-bottom: 4px;
    }
    .buttons {
      display: flex;
      gap: 5px;
      justify-content: flex-end;
      button {
        padding: 6px 12px;
        border-radius: 4px;
        border: none;
        background-color: #396aff;
        color: white;
        cursor: pointer;
        font-size: 14px;
      }
    }
  }
}

.modalContent {
  border-radius: 8px;
  background-color: white;
  padding: 12px;
  min-width: 280px;
  * {
    background-color: inherit;
    color: black;
  }
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  .buyInfo {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 20px;
    li {
      display: flex;
      align-items: center;
      height: 30px;
      span:first-child {
        width: 100px;
      }
    }
  }
  .modalButton {
    display: flex;
    gap: 5px;
    button {
      width: 50%;
      height: 40px;
      border-radius: 8px;
      border: none;
      color: white;
      cursor: pointer;
      font-size: 14px;
      &:first-child {
        background-color: #d3d3d3;
      }
      &:last-child {
        background-color: #0f0303;
      }
    }
  }
  &.addModalContent {
    display: flex;
    flex-direction: column;
    gap: 8px;
    input {
      height: 30px;
      border-radius: 8px;
      border: 1px solid #344e6a;
      padding: 0 8px;
    }
  }
}

.addButton {
  position: fixed;
  bottom: 12px;
  right: 12px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: #396aff;
  color: white;
  font-size: 32px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 5px;
}
